  vertical-align: middle;
  padding-top: 0;
  cursor: pointer;
  text-transform: none;
}

.checkbox-box {
  position: relative;
  max-width: 20px;
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  border: 2px solid #000000;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  margin-left: 0;
}

.checkbox-text {
  max-width: calc(100% - 20px);
  flex: 0 0 calc(100% - 20px);
  width: calc(100% - 20px);
  display: block;
  position: relative;
  cursor: pointer;
  text-align: left;
  vertical-align: middle;
  color: #000000;
  font-size: 16px;
  font-size: 1rem;
  padding-left: 1rem;
  line-height: 1.4;
}

.checkbox-hidden:checked + .checkbox-label .checkbox-box:after {
  content: '';
  display: block;
  position: absolute;
  top: 0px;
  left: 5px;
  width: 7px;
  height: 13px;
  border: solid #000000;
  border-width: 0 2px 2px 0;
  transform: rotate(40deg);
}

