  height: 30px;
  margin-left: 10px;
  cursor: pointer;
  vertical-align: middle;
  top: -2px;
}

.file-upload-remove:before, .file-upload-remove:after {
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  content: '';
  background: #000;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -1px;
}

.file-upload-remove:before {
  transform: rotate(45deg);
}

.file-upload-remove:after {
  transform: rotate(-45deg);
}

