.slick-dots{
	position: absolute;
	bottom: -50px;
	left: 0;
	width: 100%;
	margin: 0!important;
	text-align: center;
	li{
		position: relative;
		display: inline-block;
		width: 40px;
		height: 40px;
		padding: 0!important;
		cursor: pointer;
		vertical-align: middle;

		@include transition(all, 250ms, ease);
		&:after{
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			width: 15px;
			height: 15px;
			content: '';
			transform: translate(-50%, -50%);
			border: 2px solid $color-purple-normal;
			background-color: #ffffff;

			@include border-radius(100%);
		}
		&.slick-active,
		&:hover{
			&:after{
				border-color: $color-purple-normal;
				background-color: $color-purple-normal;
			}
		}
		button{
			&:before{
				content: none;
			}
		}
		button{
			display: none;
		}
	}
}


.slick-arrow-classic{
	.slick-arrows{
		&.slick-prev{
			left: 0;
		}
		&.slick-next{
			right: 0;
		}
	}
}

.slick-arrows{
	position: absolute;
	z-index: 4;
	top: 50%;
	display: block;
	margin-top: -25px;
	cursor: pointer;
	.icon{
		display: flex;
		width: 50px;
		height: 50px;
		text-align: center;
		color: $color-black-normal;

		align-items: center;
		justify-content: center;
		@include font-size(36px);
		&:hover{
			color: $color-purple-normal;
		}
	}
	&.slick-disabled{
		opacity: 0.2;
	}
	&.slick-prev{
		left: -50px;
	}
	&.slick-next{
		right: -50px;
	}
}